




























































import Vue from "vue";
export default Vue.extend({
  data() {
    return {
      lang: this.$i18n.locale,
    };
  },
  methods: {
    languageToggle(): void {
      if (this.$i18n.locale === "en") {
        this.$i18n.locale = "fr";
      } else {
        this.$i18n.locale = "en";
      }
    },
  },
});
